import PropTypes from 'prop-types';

// @mui
import { Box, Stack, Typography, useTheme } from '@mui/material';

// third-party
import { keys, includes } from 'lodash';

// project
import PageContainer from 'layouts/PageContainer';
import PageHeading from '../../layouts/PageHeading';

const ColorBlock = (props) => {
    const { paletteName, bgcolor, sx } = props
    return (
        <Box sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            width: 148,
            height: 52,
            p: 1,
            bgcolor,
            color: includes(paletteName, 'dark') || includes(paletteName, 'darker') || includes(paletteName, 'main') ? 'grey.100' : 'grey.700',
            ...sx
        }}>
            <Typography variant='subtitle2'>{paletteName}</Typography>
            <Typography variant='subtitle2'>
                {bgcolor}
            </Typography>
        </Box>
    )
}

const TextBlock = (props) => {
    const theme = useTheme()
    const { paletteName, bgcolor, sx } = props
    return (
        <Box sx={{
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            width: 148,
            height: 52,
            p: 1,
            bgcolor,
            color: theme.palette.mode === 'light' ? 'grey.100' : 'grey.700',
            ...sx
        }}>
            <Typography variant='subtitle2'>{paletteName}</Typography>
            <Typography variant='subtitle2'>
                {bgcolor}
            </Typography>
        </Box>
    )
}

TextBlock.propTypes = {
    bgcolor: PropTypes.string,
    paletteName: PropTypes.string,
    sx: PropTypes.oneOfType([
        PropTypes.arrayOf(
            PropTypes.oneOfType([PropTypes.func, PropTypes.object, PropTypes.bool]),
        ),
        PropTypes.func,
        PropTypes.object,
    ]),
};

const UIColors = () => {
    const theme = useTheme();
    console.log(theme)
    const mainColor = ['primary', 'secondary', 'info', 'success', 'warning', 'error']
    return (
        <PageContainer>
            <PageHeading title="Colors" />
            <Stack direction='row' spacing={2} justifyContent={'flex-start'} alignItems={'flex-start'} flexWrap={'wrap'}>
                {
                    keys(theme.palette).map((colors) => (
                        <Stack key={colors} >
                            {includes(mainColor, colors) && (
                                <ColorBlock paletteName={`${colors}`} bgcolor={theme.palette[colors]['main']} sx={{ height: 80 }} />)}

                            {includes(mainColor, colors) &&
                                keys(theme.palette[colors]).map((color) => (color !== 'contrastText' && <ColorBlock paletteName={`${color}`} bgcolor={theme.palette[colors][color]} />
                                ))}
                        </Stack>
                    ))
                }
            </Stack>


            <Typography variant='h4' mt={5} mb={2}> Text</Typography>
            <Box sx={{ pl: 6 }}>

                <Stack direction='row' spacing={2} justifyContent={'flex-start'} alignItems={'flex-start'} flexWrap={'wrap'}>
                    <TextBlock paletteName="primary" bgcolor={theme.palette.text.primary} color />
                    <TextBlock paletteName="secondary" bgcolor={theme.palette.text.secondary} color />
                    <TextBlock paletteName="disabled" bgcolor={theme.palette.text.disabled} />
                </Stack>
            </Box>

        </PageContainer>
    )
};

export default UIColors;
